.categoryname{
    width: 200px;
    text-align: center;
    background-color: #FAF3EA;
    height: 50px;
    border: 2px solid #B88E2F;
    border-radius: 50px;
    cursor: pointer;
}    
#categorybuttons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
}
.productimg{
    width: 300px;
    margin-left: 10px;
    margin-right: 10px;
    height: 300px;
}
#products {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    justify-items: center;
    margin: 0 50px;
    margin-top: 20px;
}
.card {
    background-color: #F4F5F7;
    height: auto;
    margin-top: 10px;
    margin-bottom: 20px;
}
h2 {
    color: #3a3a3a;
    margin-left: 10px;
}
p {
    color: #A8A9A9;
    margin-left: 10px;
    font-weight: bold;
}
.price {
    color: #3A3A3A;
    font-weight: bolder;
    margin-left: 10px;
}

.offer {
    color: #B0B0B0;
    text-decoration: line-through;
    margin-left: 10px;
}
#more {
    margin-left: 45%;
    color: #B88E2F;
    border: 2px solid #B88E2F;
    background-color: white;
    height: 45px;
    width: 200px;
    font-weight: bolder;
    text-align: center;
    cursor: pointer;
}
.productimg{
    cursor: pointer;
    box-shadow: 0px 2px 8px #83878e;
    transition: transform 0.2s ease;
    overflow: hidden;
}
.productimg:hover {
    transform: translateY(-10px);
    transform: translateX(-7px);
}
#more:hover{
    transition: transform 0.2s ease;
    background-color:#B88E2F ;
    color: white;
    border: 2px solid brown;
}
